* {
  box-sizing: border-box;
}

body {
  background: url(./assets/images/background.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  height: 80vh;
  width: 100vw;
}

.container {
  margin: 6% 30% auto;
  width: 40vw;
  height: 70vh;
  border: 1px solid black;
  border-radius: 10px;
  box-shadow: 5px black;
  opacity: 0.8;
  background: white;
  font-family: "PT Serif", serif;
}

.header {
  margin: 10px;
  display: flex;
  justify-content: space-between;
}

.favorites {
  display: flex;
  justify-content: flex-start;
}

.footer {
  margin-left: 65%;
  color: white;
  font-size: 15px;
  font-weight: bold;
}

.fa-heart {
  color: red;
}

.button-parent{
  display: flex;
}
.favorites-data {
  position: absolute;
  top:16%;
  left: 28%;
  width: 40%;
  border: 2px dotted #00f;
  background-color: #E0FFFF;
  color: #00f;
  border-radius: 15px;
  text-align:center;
  font-weight:bolder;
}


.favorites-data ul {
  list-style: none;
  display:block;
 
}

.favorites-data ul li:hover{
  background-color: #000;
  color: #f20;
  border-radius:25px;
  transform: scale(0.95);
}



/* Mobile Styles */
@media only screen and (max-width: 350px) {
  body {
    height: 450vh;
  }

  .container {
    height: 350vh;
  }

  
}

@media only screen and (max-width: 590px) {
  body {
    height: 250vh;
  }

  .container {
    height: 195vh;
  }
}

@media only screen and (max-width: 680px) {
  body {
    height: 242vh;
  }

  .container {
    height: 240vh;
  }
}

@media only screen and (max-width: 750px) {
  body {
    height: 240vh;
  }

  .container {
    height: 235vh;
  }
}

@media only screen and (max-width: 990px) {
  body {
    height: 260vh;
  }

  .container {
    height: 248vh;
  }
}

@media only screen and (min-width: 1024px) {
  body {
    height: 82vh;
  }

  .container {
    height: 70vh;
  }
}
